.App {
  text-align: center;
}


.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  height: 50px;
  padding: 0 20px;
  overflow-x: auto;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
}

.navbar a {
    color: white;
    text-decoration: none;
    display: inline-block;
    padding: 10px;
}
.menu-icon {
  display: none; /* hide the menu icon by default */
}

@media (max-width: 768px) {
  body {
    overflow-x: hidden;
  }
  /* display the menu icon only when screen width is less than or equal to 768px */
  .menu-icon {
    color: white;
    display: block;
    cursor: pointer;
    position: absolute; /* add this line */
    top: 0; /* add this line */
    left: 0; /* add this line */
  }
  
  /* hide the links in the navbar by default */
  .navbar a {
    display: none;
  }
  
  /* show the links in the navbar when the navbar is open */
  .navbar.open a {
    display: block;
  }
  
  /* add some styles to the menu icon */
  .menu-icon {
    padding-top: 7px;
    padding-left: 15px;
    font-size: 24px;
    margin-right: 16px;
  }
  
  /* modify the layout of the navbar to display links vertically */
  .navbar.open {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow-y: auto;
    justify-content: flex-start;
  }
  
  /* add some styles to the expanded navbar */
  .navbar.open {
    display: flex;
  }
}




button {
  color: #fff;
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

button:hover {
  background-color: #555;
}
